import gsap from "gsap";
import { ScrollTrigger } from "gsap/all";

// Register GSAP plugins
gsap.registerPlugin(ScrollTrigger);

export function initHeroAnimations() {
  // Set clean initial states
  gsap.set("#header", { 
    y: 40, 
    opacity: 0
  });
  
  gsap.set("#header #banana", { 
    scale: 1,
    transformOrigin: "center center"
  });
  
  gsap.set(".font-pally", { 
    y: 20, 
    opacity: 0 
  });
  
  gsap.set("button", { 
    y: 20,
    opacity: 0
  });
  
  gsap.set("#eclipse", { 
    opacity: 0
  });
  
  // Set gorilla initial states - simple and clean
  const gorillas = [
    "#ape1", "#ape2", "#ape3", "#ape4", 
    "#goldie", "#ape5", "#ape6", "#ape7", "#ape8"
  ];
  
  gorillas.forEach((ape) => {
    gsap.set(ape, {
      y: 30,
      opacity: 0
    });
  });

  // Create clean, simple timeline
  const heroTL = gsap.timeline({
    delay: 0.3
  });

  // 1. Header fade up
  heroTL.to("#header", {
    duration: 0.8,
    y: 0,
    opacity: 1,
    ease: "power2.out",
  })

  // 2. Subtle banana emphasis
  .to("#banana", {
    duration: 0.4,
    scale: 1.05,
    ease: "power2.out",
  }, "-=0.3")
  
  .to("#banana", {
    duration: 0.3,
    scale: 1,
    ease: "power2.out",
  })

  // 3. Subtitle fade up
  .to(".font-pally", {
    duration: 0.6,
    y: 0,
    opacity: 1,
    ease: "power2.out",
  }, "-=0.4")

  // 4. Button fade up
  .to("button", {
    duration: 0.6,
    y: 0,
    opacity: 1,
    ease: "power2.out",
  }, "-=0.3")

  // 5. Eclipse fade in
  .to("#eclipse", {
    duration: 0.8,
    opacity: 0.8,
    ease: "power2.out",
  }, "-=0.5")

  // 6. Gorillas simple fade up
  .to(gorillas, {
    duration: 0.8,
    y: 0,
    opacity: 1,
    ease: "power2.out",
    stagger: {
      amount: 0.4,
      from: "center"
    }
  }, "-=0.4");

  // Add minimal effects
  addSubtleEffects();
  addCleanHoverEffects();
  addSmoothScrollEffects();
}

function addSubtleEffects() {
  // Very subtle banana pulse
  gsap.to("#banana", {
    scale: 1.02,
    duration: 3,
    ease: "sine.inOut",
    repeat: -1,
    yoyo: true
  });

  // Gentle eclipse rotation
  gsap.to("#eclipse", {
    rotation: 2,
    duration: 12,
    ease: "none",
    repeat: -1,
    yoyo: true
  });
}

function addHoverEffects() {
  // Header hover effect
  const header = document.querySelector("#header");
  if (header) {
    header.addEventListener("mouseenter", () => {
      gsap.to("#banana", {
        scale: 1.1,
        rotation: 5,
        duration: 0.3,
        ease: "back.out(1.7)"
      });
    });
    
    header.addEventListener("mouseleave", () => {
      gsap.to("#banana", {
        scale: 1,
        rotation: 0,
        duration: 0.3,
        ease: "power2.out"
      });
    });
  }

  // Button hover effect with particle-like animation
  const button = document.querySelector("button");
  if (button) {
    button.addEventListener("mouseenter", () => {
      gsap.to(button, {
        scale: 1.05,
        y: -2,
        duration: 0.3,
        ease: "power2.out"
      });
    });
    
    button.addEventListener("mouseleave", () => {
      gsap.to(button, {
        scale: 1,
        y: 0,
        duration: 0.3,
        ease: "power2.out"
      });
    });
  }

  // Gorilla hover effects
  const gorillas = document.querySelectorAll('[id^="ape"], #goldie');
  gorillas.forEach(ape => {
    ape.addEventListener("mouseenter", () => {
      gsap.to(ape, {
        scale: 1.1,
        rotation: 10,
        z: 20,
        duration: 0.4,
        ease: "back.out(1.7)"
      });
    });
    
    ape.addEventListener("mouseleave", () => {
      gsap.to(ape, {
        scale: 1,
        rotation: 0,
        z: 0,
        duration: 0.4,
        ease: "power2.out"
      });
    });
  });
}

function addScrollAnimations() {
  // Parallax effect for hero elements
  gsap.to("#header", {
    yPercent: -50,
    ease: "none",
    scrollTrigger: {
      trigger: ".hero-section",
      start: "top bottom",
      end: "bottom top",
      scrub: 1
    }
  });

  gsap.to("#eclipse", {
    yPercent: 30,
    rotation: 45,
    scale: 1.2,
    ease: "none",
    scrollTrigger: {
      trigger: ".hero-section",
      start: "top bottom",
      end: "bottom top",
      scrub: 1
    }
  });

  // Gorillas parallax with different speeds
  const gorillas = [
    "#ape1", "#ape2", "#ape3", "#ape4", 
    "#goldie", "#ape5", "#ape6", "#ape7", "#ape8"
  ];
  
  gorillas.forEach((ape, index) => {
    gsap.to(ape, {
      yPercent: (index - 4) * 10, // Different parallax speeds
      rotation: (index - 4) * 2,
      ease: "none",
      scrollTrigger: {
        trigger: ".hero-section",
        start: "top bottom",
        end: "bottom top",
        scrub: 1
      }
    });
  });

  // Button scale down on scroll
  gsap.to("button", {
    scale: 0.9,
    opacity: 0.8,
    ease: "none",
    scrollTrigger: {
      trigger: ".hero-section",
      start: "center center",
      end: "bottom top",
      scrub: 1
    }
  });
}

// Advanced entrance animation with particles effect
export function createParticleEffect() {
  const particleContainer = document.createElement('div');
  particleContainer.className = 'particle-container';
  particleContainer.style.cssText = `
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
    z-index: 5;
  `;
  
  document.querySelector('.hero-section').appendChild(particleContainer);
  
  // Create floating particles
  for (let i = 0; i < 20; i++) {
    const particle = document.createElement('div');
    particle.style.cssText = `
      position: absolute;
      width: 4px;
      height: 4px;
      background: #FFCE3C;
      border-radius: 50%;
      opacity: 0;
    `;
    
    particleContainer.appendChild(particle);
    
    gsap.set(particle, {
      x: `random(0, ${window.innerWidth})`,
      y: `random(${window.innerHeight}, ${window.innerHeight + 100})`,
    });
    
    gsap.to(particle, {
      y: -100,
      opacity: 1,
      duration: `random(3, 6)`,
      ease: "none",
      repeat: -1,
      delay: `random(0, 3)`,
    });
  }
}